/*! tailwindcss v4.1.4 | MIT License | https://tailwindcss.com */
@layer theme, base, components, utilities;
@layer utilities {
  .antialiased {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
}
* {
  box-sizing: border-box;
}
body,
body::before,
body::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
a {
  text-decoration: none;
  color: inherit;
}
@font-face {
  font-family: Icon Font;
  src: url(/fonts/brand-title.woff2) format("woff2"),
    url(/fonts/brand-title.woff2) format("woff");
}
.font-brand-title {
  font-family: "Icon Font";
  text-underline-position: from-font;
  text-decoration-skip-ink: none;
}
.font-regular-title-bold {
  font-family: Avenir Next W01, Avenir Next W00, IBM Plex Sans, sans-serif,
    Helvetica Neue, Helvetica, Roboto, Arial;
  text-underline-position: from-font;
  text-decoration-skip-ink: none;
  font-weight: 600;
}
.font-regular-title-regular {
  font-family: Avenir Next W01, Avenir Next W00, IBM Plex Sans, sans-serif,
    Helvetica Neue, Helvetica, Roboto, Arial;
  text-underline-position: from-font;
  text-decoration-skip-ink: none;
  font-weight: 400;
}
